@import './utils/normilize';
@import './utils/colors';
@import './blocks/container';
@import './utils/reset';
@import './blocks/stars';

.product-card {
  box-sizing: border-box;
  width: 200px;
  border: 1px solid $color__background_secondary;

  display: flex;
  flex-direction: column;

  padding: 32px 16px 16px;
  border-radius: 5px;
  color: $color__font_primary;
  background-color: $color__background_primay;

  &__img {
    margin: 0 auto 40px;
    width: 160px;
    height: 134px;
  }

  &__title {
    margin-bottom: 4px;

    font-size: 12px;
    font-weight: 500;
  }

  &__code {
    color: $color__font_secondary;

    font-size: 10px;
    line-height: 14px;

    margin-bottom: 16px;
  }

  &__rate {
    width: 100%;
    display: flex;
    margin-bottom: 24px;
    justify-content: space-between;
    align-items: center;
  }

  &__reviews {
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 10px;
    line-height: 14px;
    text-align: center;
  }

  &__price {
    display: flex;
    justify-content: space-between;
    align-items: center;

    margin-bottom: 16px;

    &_name {
      font-size: 12px;
      color: $color__font_secondary;
    }

    &_cost {
      font-size: 16px;
      font-weight: 700;
    }
  }

  &__button {
    box-sizing: border-box;
    border-radius: 5px;
    border: 1px $color__buyButton_secondary solid;
    width: 166px;
    height: 40px;

    color: $color__font_white;
    background-color: $color__buyButton_secondary;
    cursor: pointer;

    font-weight: 700;
    text-transform: uppercase;
    text-align: center;
    line-height: 40px;

    transition: 0.3s;

    &:hover {
      color: $color__buyButton_secondary;
      border: 1px $color__buyButton_secondary solid;
      background-color: $color__font_white;
    }
  }
}
